.app {
   height: 100vh;
   font-family: 'Poppins Regular';
}

main {
   width: 90vw;
   margin: auto;
}

button {
   border-radius: 10px;
}

/* Header */

.app-header {
   width: 100%;
   height: 7vh;
   background-color: rgb(25, 25, 25);
   display: flex;
   justify-content: center;
   align-items: center;
}

.app-header h2 {
   font-family: 'Poppins Black';
   margin: 0;
   font-size: 2em;
   letter-spacing: 2px;
   color: rgb(225, 225, 225);
}

.section-headers {
   display: flex;
   justify-content: center;
}

.section-headers h2 {
   font-family: 'Poppins Black';
   margin: 0;
   font-size: 2em;
   letter-spacing: 2px;
   color: rgb(25, 25, 25);
}

/* Products */

.products {
   padding: 50px 0;
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
}

.product-container {
   width: 300px;
   height: fit-content;
   margin: 5px;
   box-shadow: 0 5px 30px #cccccc;
   border-radius: 15px;
}

.product-headings {
   width: 90%;
   margin: auto;
   text-transform: capitalize;
}

.product-headings > * {
   margin: 0;
}

.product-details {
   width: 90%;
   margin: auto;
}

.product-details span {
   color: grey;
   user-select: none;
   cursor: pointer;
}

.product-description {
   padding-bottom: 10px;
}

.product-image-container {
   margin: auto;
   width: 75%;
   height: 75%;
}

.product-image-container img {
   max-width: 100%;
   max-height: 100%;
}

.product-container button {
   display: block;
   background-color: #728840;
   color: white;
   border: none;
   font-family: 'Poppins Regular';
   font-size: 1.5em;
   padding: 0 10px;
   cursor: pointer;
}

/* Shopping cart */

.shopping-cart {
   padding: 0 0 25px 0;
}

.no-products {
   display: flex;
   justify-content: center;
}

.all-products {
   margin: 25px 0;
}

.shopping-cart-container {
   max-width: 400px;
   margin: auto;
}

.shopping-cart-product {
   display: flex;
   width: 100%;
   height: 100px;
   box-shadow: 0 5px 30px #cccccc;
   border-radius: 15px;
   margin: 5px 0;
}

.shopping-cart-product-image-container {
   width: 100px;
   height: 100%;
}

.shopping-cart-product-image-container img {
   max-width: 100%;
   max-height: 100%;
}

.shopping-cart-text h3 {
   margin: 0;
   text-transform: capitalize;
}

.shopping-cart-bottom-total-empty {
   display: flex;
   justify-content: space-evenly;
   align-items: center;
}

.shopping-cart-bottom-total-empty > * {
   padding: 0 5px;
}

.empty-cart {
   border: none;
   font-family: 'Poppins Regular';
   background-color: #e81426;
   color: white;
   padding: 8px;
   font-size: 1em;
   cursor: pointer;
}

.total {
   width: fit-content;
}

/* Confirm order */

.confirm-order-container {
   display: flex;
   justify-content: center;
}

.confirm-order-container button {
   background-color: rgb(25, 25, 25);
   color: rgb(225, 225, 225);
   font-family: 'Poppins Regular';
   padding: 8px;
   font-size: 1em;
   cursor: pointer;
}

.confirm-order {
   padding: 0 0 25px 0;
}

.confirm-order form {
   width: 500px;
   margin: auto;
}

.confirm-order input {
   padding: 5px;
   border-radius: 5px;
   font-size: 1em;
}

.confirm-order input[type='text'] {
   width: 200px;
}

.confirm-order input[type='email'] {
   width: 200px;
}

.confirm-order input[type='tel'] {
   width: 150px;
}

.confirm-order input[type='submit'] {
   width: 100px;
   padding: 8px;
   font-family: 'Poppins Regular';
   background-color: #1d4ed8;
   color: white;
   border: none;
}

.confirm-order label {
   display: block;
}

.personal-information {
   display: flex;
   flex-wrap: wrap;
}

.confirm-order form p {
   margin: 25px 0 0 0;
}

.row {
   display: flex;
   width: 100%;
}

.row > div {
   margin: 10px 72px 0 0;
}

.horizontal-line-text {
   font-family: 'Poppins Bold';
}

/* Order summary */

.order-summary {
   margin: auto;
   width: 50%;
}

.order-summary-container h3 {
  margin: 0;
  font-size: 1.5em;
}

.order-summary-container p {
   margin: 5px 0 0 0;
}

/* Media tags */

@media screen and (max-width: 690px) {
   main {
      width: 100%;
   }
}
